'use client';

import { useEffect, useState } from 'react';
import Layout from '@/components/Layout';
import { analyticsApi } from '@/lib/api';
import { ChartBarIcon, DocumentTextIcon, ExclamationTriangleIcon } from '@heroicons/react/24/outline';
import { BanknotesIcon } from '@heroicons/react/24/solid';
import Link from 'next/link';
import toast from 'react-hot-toast';

interface Summary {
  total_transactions: number;
  total_spend: number;
  avg_transaction: number;
  total_documents: number;
}

export default function Home() {
  const [summary, setSummary] = useState<Summary | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchSummary = async () => {
      try {
        const data = await analyticsApi.getSummary();
        setSummary(data);
      } catch (error: any) {
        console.error('Failed to load summary:', error);
        // Set default values on error
        setSummary({
          total_transactions: 0,
          total_spend: 0,
          avg_transaction: 0,
          total_documents: 0,
        });
        if (error.response?.status !== 404) {
          toast.error('Failed to load summary');
        }
      } finally {
        setLoading(false);
      }
    };
    fetchSummary();
  }, []);

  const stats = [
    {
      name: 'Total Transactions',
      value: summary?.total_transactions.toLocaleString() || '0',
      icon: ChartBarIcon,
      href: '/analytics',
      gradient: 'from-blue-500 to-cyan-500',
      bgGradient: 'from-blue-50 to-cyan-50',
    },
    {
      name: 'Total Spending',
      value: `$${summary?.total_spend.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 }) || '0.00'}`,
      icon: BanknotesIcon,
      href: '/analytics',
      gradient: 'from-emerald-500 to-teal-500',
      bgGradient: 'from-emerald-50 to-teal-50',
    },
    {
      name: 'Documents',
      value: summary?.total_documents.toLocaleString() || '0',
      icon: DocumentTextIcon,
      href: '/documents',
      gradient: 'from-purple-500 to-pink-500',
      bgGradient: 'from-purple-50 to-pink-50',
    },
    {
      name: 'Avg Transaction',
      value: `$${summary?.avg_transaction.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 }) || '0.00'}`,
      icon: BanknotesIcon,
      href: '/analytics',
      gradient: 'from-amber-500 to-orange-500',
      bgGradient: 'from-amber-50 to-orange-50',
    },
  ];

  return (
    <Layout>
      <div className="space-y-8">
        {/* Hero Section */}
        <div className="relative overflow-hidden rounded-3xl bg-gradient-to-br from-indigo-600 via-purple-600 to-pink-600 p-12 text-white shadow-2xl">
          <div className="absolute inset-0 bg-[url('data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iNjAiIGhlaWdodD0iNjAiIHZpZXdCb3g9IjAgMCA2MCA2MCIgeG1sbnM9Imh0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnIj48ZyBmaWxsPSJub25lIiBmaWxsLXJ1bGU9ImV2ZW5vZGQiPjxnIGZpbGw9IiNmZmYiIGZpbGwtb3BhY2l0eT0iMC4xIj48Y2lyY2xlIGN4PSIzMCIgY3k9IjMwIiByPSIyIi8+PC9nPjwvZz48L3N2Zz4=')] opacity-20"></div>
          <div className="relative z-10">
            <h1 className="text-4xl md:text-5xl font-bold mb-4 bg-clip-text text-transparent bg-gradient-to-r from-white to-indigo-100">
              Welcome to Insight Agent
            </h1>
            <p className="text-xl text-indigo-100 max-w-2xl">
              AI-Powered Intelligent Document Processing with advanced analytics, insights, and automated data extraction
            </p>
          </div>
        </div>

        {/* Quick Stats */}
        {loading ? (
          <div className="grid grid-cols-1 gap-6 sm:grid-cols-2 lg:grid-cols-4">
            {[1, 2, 3, 4].map((i) => (
              <div key={i} className="bg-white rounded-2xl shadow-lg p-6 animate-pulse">
                <div className="h-24 bg-slate-200 rounded-xl"></div>
              </div>
            ))}
          </div>
        ) : (
          <div className="grid grid-cols-1 gap-6 sm:grid-cols-2 lg:grid-cols-4">
            {stats.map((stat) => (
              <Link
                key={stat.name}
                href={stat.href}
                className="group relative overflow-hidden bg-white rounded-2xl shadow-lg hover:shadow-2xl transition-all duration-300 hover:-translate-y-1 border border-slate-100"
              >
                <div className={`absolute inset-0 bg-gradient-to-br ${stat.bgGradient} opacity-0 group-hover:opacity-100 transition-opacity duration-300`}></div>
                <div className="relative p-6">
                  <div className="flex items-center justify-between mb-4">
                    <div className={`p-3 rounded-xl bg-gradient-to-br ${stat.gradient} shadow-lg`}>
                      <stat.icon className="h-6 w-6 text-white" />
                    </div>
                  </div>
                  <p className="text-sm font-semibold text-slate-600 mb-1">{stat.name}</p>
                  <p className="text-3xl font-bold text-slate-900">{stat.value}</p>
                </div>
              </Link>
            ))}
          </div>
        )}

        {/* Quick Actions */}
        <div className="bg-white rounded-2xl shadow-lg p-8 border border-slate-100">
          <h2 className="text-2xl font-bold text-slate-900 mb-6">Quick Actions</h2>
          <div className="grid grid-cols-1 gap-4 sm:grid-cols-2 lg:grid-cols-3">
            <Link
              href="/documents"
              className="group relative overflow-hidden flex items-center p-6 border-2 border-slate-200 rounded-xl hover:border-indigo-500 hover:shadow-xl transition-all duration-300 bg-gradient-to-br from-white to-slate-50"
            >
              <div className="flex-shrink-0">
                <div className="bg-gradient-to-br from-indigo-500 to-purple-500 p-4 rounded-xl shadow-lg group-hover:scale-110 transition-transform duration-300">
                  <DocumentTextIcon className="h-6 w-6 text-white" />
                </div>
              </div>
              <div className="ml-4 flex-1">
                <div className="font-bold text-slate-900 group-hover:text-indigo-600 transition-colors">Upload Document</div>
                <div className="text-sm text-slate-600 mt-1">Process new invoices or receipts</div>
              </div>
            </Link>
            <Link
              href="/analytics"
              className="group relative overflow-hidden flex items-center p-6 border-2 border-slate-200 rounded-xl hover:border-emerald-500 hover:shadow-xl transition-all duration-300 bg-gradient-to-br from-white to-slate-50"
            >
              <div className="flex-shrink-0">
                <div className="bg-gradient-to-br from-emerald-500 to-teal-500 p-4 rounded-xl shadow-lg group-hover:scale-110 transition-transform duration-300">
                  <ChartBarIcon className="h-6 w-6 text-white" />
                </div>
              </div>
              <div className="ml-4 flex-1">
                <div className="font-bold text-slate-900 group-hover:text-emerald-600 transition-colors">View Analytics</div>
                <div className="text-sm text-slate-600 mt-1">Explore spending trends and insights</div>
              </div>
            </Link>
            <Link
              href="/anomalies"
              className="group relative overflow-hidden flex items-center p-6 border-2 border-slate-200 rounded-xl hover:border-red-500 hover:shadow-xl transition-all duration-300 bg-gradient-to-br from-white to-slate-50"
            >
              <div className="flex-shrink-0">
                <div className="bg-gradient-to-br from-red-500 to-pink-500 p-4 rounded-xl shadow-lg group-hover:scale-110 transition-transform duration-300">
                  <ExclamationTriangleIcon className="h-6 w-6 text-white" />
                </div>
              </div>
              <div className="ml-4 flex-1">
                <div className="font-bold text-slate-900 group-hover:text-red-600 transition-colors">Check Anomalies</div>
                <div className="text-sm text-slate-600 mt-1">Detect duplicates and issues</div>
              </div>
            </Link>
          </div>
        </div>
      </div>
    </Layout>
  );
}
